@import 'normalize.css';
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/RobotoRegular.woff2") format("woff2"), url("../fonts/RobotoRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/RobotoBold.woff2") format("woff2"), url("../fonts/RobotoBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Firs Neue";
  src: local("TT Firs Neue Regular"), local("TTFirsNeue-Regular"), url("../fonts/TTFirsNeueRegular.woff2") format("woff2"), url("../fonts/TTFirsNeueRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Firs Neue";
  src: local("TT Firs Neue Bold"), local("TTFirsNeue-Bold"), url("../fonts/TTFirsNeueBold.woff2") format("woff2"), url("../fonts/TTFirsNeueBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Firs Neue";
  src: local("TT Firs Neue Medium"), local("TTFirsNeue-Medium"), url("../fonts/TTFirsNeueMedium.woff2") format("woff2"), url("../fonts/TTFirsNeueMedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.page {
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: #FFFFFF;
}

/* glob */
.btn-reset {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.btn {
  display: inline-block;
  padding: 15.5px 22px;
  outline: none;
  font-weight: 700;
  font-size: 16px;
  line-height: normal;
  text-align: center;
}

.btn--orange {
  color: #FFFFFF;
  background-color: #ff6e30;
  transition: outline 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.btn--orange:focus-visible {
  outline: 3px solid #ce4307;
}

.btn--orange:hover {
  background-color: #bc572b;
}

.btn--orange:active {
  outline: none;
  background-color: #ce4307;
}

.btn--transparent {
  outline: 3px solid #ff6e30;
  color: #FFFFFF;
  background-color: transparent;
  transition: outline 0.3s ease-in-out, background-color 0.3s ease-in-out;
}

.btn--transparent:focus-visible {
  background-color: #bc572b;
}

.btn--transparent:hover {
  outline: none;
  background-color: #ff6e30;
}

.btn--transparent:active {
  outline: none;
  background-color: #ce4307;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 1320px;
  max-width: 100%;
}

/* header */
.header {
  padding-top: 24px;
  padding-bottom: 21px;
  background-color: #202020;
}
.header__container {
  align-items: center;
}
.header__logo {
  flex-shrink: 0;
  margin-right: auto;
  transition: outline 0.3s ease-in-out;
}
.header__logo:focus-visible {
  outline-offset: 5px;
  outline: 2px solid #bc572b;
}
.header__img {
  width: 190.25px;
  height: 30.7px;
}
.header__nav {
  margin-right: 188px;
}
.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.header__item:not(:last-child) {
  margin-right: 69px;
}
.header__link {
  padding: 0 5px;
  font-family: "TT Firs Neue", sans-serif;
  font-size: 16px;
  line-height: normal;
  color: #FFFFFF;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
.header__link:focus-visible {
  outline: none;
  background-color: #bc572b;
}
.header__link:hover {
  color: #ff6e30;
}
.header__link:active {
  color: #ce4307;
  background-color: transparent;
}
.header__search {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.header__search svg {
  fill: #FFFFFF;
  transition: fill 0.3s ease-in-out;
}
.header__search:focus-visible {
  outline: none;
  background-color: #bc572b;
}
.header__search:hover svg {
  fill: #ff6e30;
}
.header__search:active {
  background-color: transparent;
}
.header__search:active svg {
  fill: #ce4307;
}

.burger {
  display: none;
}

/* hero */
.hero {
  /*&__container {*/
  padding-top: 63px;
  padding-bottom: 58px;
  /*}*/
}
.hero__future {
  align-items: flex-end;
  position: relative;
  grid-column-start: 1;
  grid-column-end: 7;
  max-height: 630px;
  background-image: url("../img/hero__future.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.hero__hashtag {
  position: absolute;
  font-size: 14px;
  text-align: center;
  color: #FFFFFF;
  background-color: #202020;
  top: 15px;
  right: 15px;
  padding: 7px 15px 7px 14px;
}
.hero__content {
  flex-direction: column;
  align-items: flex-start;
  max-width: 482px;
  padding-left: 58px;
  padding-bottom: 60px;
}
.hero__master-class {
  margin-bottom: 3px;
  font-size: 16px;
  color: #FFFFFF;
}
.hero__neon {
  margin-bottom: 19px;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 104%;
  color: #FFFFFF;
}
.hero__btn {
  padding: 15.5px 24px;
}
.hero__previous {
  grid-column-start: 7;
  grid-column-end: 13;
  flex-direction: column;
}
.hero__title {
  margin: 0;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
  color: #202020;
  margin-bottom: 18px;
}
.hero__previous-big {
  margin-bottom: 26px;
}
.hero__previous-small {
  gap: 30px;
}

/* studio */
.studio {
  padding-top: 30px;
  padding-bottom: 66px;
  background-color: #202020;
}
.studio__title {
  margin: 0;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
  color: #202020;
  margin-bottom: 6px;
  line-height: normal;
  color: #FFFFFF;
}
.studio__columns {
  justify-content: space-between;
  flex-wrap: wrap;
}
.studio__column {
  flex-direction: column;
  width: calc((100% - 30px) / 2);
}
.studio__descr {
  margin: 0;
  font-size: 16px;
  line-height: 137%;
  color: #cacaca;
  line-height: 175%;
}
.studio__column--2 {
  justify-content: flex-end;
}
.studio__img {
  margin-bottom: 21px;
}
.studio__newsletter {
  margin-bottom: 16px;
  font-family: "TT Firs Neue", sans-serif;
  font-size: 24px;
  line-height: 92%;
  color: #FFFFFF;
}
.studio__input {
  margin-right: 30px;
  padding-top: 15px;
  padding-bottom: 16px;
  padding-left: 23px;
  border: none;
  width: 100%;
  font-size: 16px;
  line-height: normal;
  background-color: rgba(74, 74, 74, 0.85);
  color: #cacaca;
  transition: outline 0.3s ease-in-out, color 0.3s ease-in-out;
}
.studio__input:focus-visible {
  outline: 1px solid #ff6e30;
}
.studio__input:hover placeholder {
  color: #ff6e30;
}
.studio__input:active {
  outline: none;
  color: #FFFFFF;
}

/* projects */
.projects {
  padding-top: 63px;
  padding-bottom: 48px;
}
.projects__title {
  margin: 0;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
  color: #202020;
  margin-bottom: 12px;
  line-height: normal;
  color: #202020;
}
.projects__container {
  grid-template-rows: repeat(2, 1fr);
  row-gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.projects__item--1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}
.projects__item--2 {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
}
.projects__item--3 {
  grid-column-start: 5;
  grid-column-end: 7;
  grid-row-start: 1;
  grid-row-end: 2;
}
.projects__item--4 {
  grid-column-start: 7;
  grid-column-end: 13;
  grid-row-start: 1;
  grid-row-end: 2;
}
.projects__item--5 {
  grid-column-start: 1;
  grid-column-end: 7;
  grid-row-start: 2;
  grid-row-end: 3;
}
.projects__item--6 {
  grid-column-start: 7;
  grid-column-end: 10;
  grid-row-start: 2;
  grid-row-end: 3;
}
.projects__item--7 {
  grid-column-start: 10;
  grid-column-end: 13;
  grid-row-start: 2;
  grid-row-end: 3;
}

.project {
  position: relative;
  flex-direction: column;
  width: 100%;
}
.project__hashtag {
  position: absolute;
  font-size: 14px;
  text-align: center;
  color: #FFFFFF;
  background-color: #202020;
  top: 7px;
  right: 7px;
  padding: 6px 14px 6px 14px;
}
.project__img {
  margin-bottom: 13px;
}
.project__descr {
  margin: 0;
  font-size: 16px;
  line-height: 137%;
  color: #cacaca;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 11px;
  color: #202020;
}
.project__time {
  padding-left: 18px;
  font-size: 12px;
  line-height: 183%;
  color: #999;
  background-image: url("../img/projects.svg");
  background-repeat: no-repeat;
  background-position: left center;
}
.project--4 {
  background-image: url("../img/projects-4.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.project__content {
  flex-direction: column;
  align-items: flex-start;
  max-width: 321px;
  padding-top: 22px;
  padding-bottom: 30px;
  padding-left: 30px;
}
.project__time--top {
  margin-bottom: 1px;
  color: #FFFFFF;
  background-image: url("../img/projects-top.svg");
}
.project__subtitle {
  margin: 0;
  margin-bottom: 6px;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  color: #FFFFFF;
}
.project__descr--4 {
  margin-bottom: 55px;
  color: #FFFFFF;
}
.project__btn {
  padding: 13px 20px;
}
.project--5 {
  background-image: url("../img/projects-5.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.project__descr--5 {
  margin-bottom: 101px;
  color: #FFFFFF;
}

/* contacts */
.contacts {
  padding-top: 14px;
  padding-bottom: 64px;
}
.contacts__title {
  margin: 0;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 104%;
  color: #202020;
  margin-bottom: 20px;
  line-height: normal;
  color: #202020;
}
.contacts__columns {
  align-items: flex-end;
  justify-content: space-between;
  flex-wrap: wrap;
}
.contacts__column {
  flex-direction: column;
}
.contacts__column--1 {
  position: relative;
  width: 66%;
  background-image: url("../img/contacts.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.contacts__content {
  flex-direction: column;
  padding: 175px 50px 149px 60px;
  width: 48%;
  background-color: rgba(32, 32, 32, 0.85);
  background-image: url("../img/contacts-1.svg");
  background-repeat: no-repeat;
  background-position: 95% 20px;
}
.contacts__subtitle {
  margin: 0;
  margin-bottom: 1px;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 92%;
  color: #FFFFFF;
}
.contacts__address {
  font-style: normal;
  font-size: 16px;
  line-height: 175%;
  color: #cacaca;
}
.contacts__link {
  display: block;
  padding-left: 22px;
  font-family: "TT Firs Neue", sans-serif;
  font-size: 16px;
  color: #ff6e30;
  background-image: url("../img/contacts-2.svg");
  background-repeat: no-repeat;
  background-position: left center;
  transition: outline 0.3s ease-in-out;
}
.contacts__link:focus-visible {
  outline-offset: 5px;
  outline: 2px solid #bc572b;
}
.contacts__location {
  position: absolute;
  left: 72.7%;
  top: 205px;
  width: 12px;
  height: 12px;
}
.contacts__column--2 {
  width: 32%;
}
.contacts__subtitle--black {
  color: #202020;
}
.contacts__descr {
  margin: 0;
  font-size: 16px;
  line-height: 137%;
  color: #cacaca;
  color: #999;
  margin-bottom: 14px;
  max-width: 397px;
}
.contacts__form-list {
  flex-direction: column;
}
.contacts__form-item {
  margin-bottom: 20px;
}
.contacts__form-fields {
  padding-top: 15px;
  padding-bottom: 16px;
  padding-left: 23px;
  border: none;
  width: 100%;
  font-size: 16px;
  line-height: normal;
  color: #999;
  background-color: #e9e9e9;
  transition: color 0.3s ease-in-out, outline 0.3s ease-in-out;
}
.contacts__form-fields:focus-visible {
  outline: 1px solid #ff6e30;
}
.contacts__form-fields:hover {
  color: #ff6e30;
}
.contacts__form-fields:active {
  color: #202020;
  outline: none;
}
.contacts__form-fields--textarea {
  padding-bottom: 96px;
}

/* footer */
.footer {
  padding-top: 25px;
  padding-bottom: 25px;
  background: #202020;
}
.footer__container {
  justify-content: space-between;
  flex-wrap: wrap;
}
.footer__column {
  flex-direction: column;
}
.footer__column-left {
  align-items: flex-start;
  margin-right: -126px;
}
.footer__logo {
  flex-shrink: 0;
  margin-bottom: 4px;
  transition: outline 0.3s ease-in-out;
}
.footer__logo:focus-visible {
  outline-offset: 5px;
  outline: 2px solid #bc572b;
}
.footer__img {
  width: 190.25px;
  height: 30.7px;
}
.footer__fotostudio {
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #e9e9e9;
}
.footer__column-center {
  align-items: flex-start;
}
.footer__descr-center {
  margin: 0;
  font-size: 16px;
  line-height: 137%;
  color: #cacaca;
  max-width: 361px;
}
.footer__column-right {
  align-items: center;
}
.footer__descr-right {
  margin: 0;
  font-size: 16px;
  line-height: 137%;
  color: #cacaca;
  margin-bottom: 8px;
}
.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: center;
  gap: 20px;
}
.footer__social-link svg {
  fill: #cacaca;
  transition: fill 0.3s ease-in-out;
}
.footer__social-link:focus-visible {
  outline: none;
}
.footer__social-link:focus-visible svg {
  fill: #bc572b;
}
.footer__social-link:hover svg {
  fill: #ff6e30;
}
.footer__social-link:active svg {
  fill: #ce4307;
}

@media (max-width: 1440px) {
  .header__nav {
    margin-right: 126px;
  }
  .header__item:not(:last-child) {
    margin-right: 23.4px;
  }
  .footer__descr-right {
    margin-left: -80px;
  }
  .footer__social-list {
    margin-left: -80px;
  }
}
