@media (max-width: 992px) {
  .header {
    padding-bottom: 0;
  }
  .header__container {
    flex-wrap: wrap;
  }
  .header__logo {
    margin-bottom: 22px;
  }
  .header__nav {
    order: 1;
    margin-right: -31px;
    margin-left: -31px;
